.copyright svg {
  fill: #000000;
  display: block;
}

.copyright {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;


  &:hover {
    color: #81b3d2;
  }

  &:hover svg {
    fill: #81b3d2;
  }

  &:active {
    color: #b3b3b3;
  }

  &:active svg {
    fill: #b3b3b3;
  }
}

.copyright__text {
  color: #000000;
  margin-right: 12px;
}
