.post {
  list-style-type: none;
  padding: 25px 18px 27px;
  position: relative;

  @media (max-width: @mobile-width-only) {
    padding: 26px 18px 28px;
    border-top: 1px solid #e5e5e5;
  }
}

.post__wrapper {
  @media (min-width: @tablet-width) {
    padding: 22px 20px 15px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    background-color: #f2f2f2;
  }

  &--dark {
    @media (min-width: @tablet-width) {
      background-color: #eaeaea;
    }
  }

  @media (min-width: @desktop-width) {
    width: 100%;
    background-color: #0000004d;
    position: absolute;
    bottom: 0;
    color: #ffffff;
    align-items: center;
    padding-left: 23px;
  }
}

.post__content {
  @media (max-width: @mobile-width-only) {
    margin-bottom: 12px;
  }
}

.post__title {
  text-align: left;
  margin-bottom: 0;

  @media (min-width: @desktop-width) {
    margin-bottom: 3px;
  }
}

.post__counter {
  font-weight: 700;
  margin-left: 14px;
  line-height: 1;

  @media (min-width: @tablet-width) {
    margin-left: 10px;
  }
}

.post__description {
  text-transform: none;
  color: #333333;
  margin-bottom: 0;
  margin-top: 1px;

  @media (min-width: @desktop-width) {
    color: #ffffff;
  }

  @media (min-width: @desktop-width) {
    margin-top: 0;
  }
}

.post__img {
  width: 100%;
  height: 280px;
  margin-bottom: 14px;

  @media (min-width: @tablet-width) {
    margin-bottom: 0;
    height: 267px;
  }
}

.post:first-child .post__img {
  @media (min-width: @tablet-width) {
    height: 400px;
  }
}

.post img {
  display: block;
  width: 100%;

  @media (min-width: @tablet-width) {
    height: 100%;
  }
}

.post__like {
  display: flex;
  align-items: flex-end;

  @media (min-width: @tablet-width) {
    padding: 3px 0 0;
  }
}

.post__like svg {
  opacity: 0.3;
  fill: #000000;

  @media (min-width: @desktop-width) {
    opacity: 1;
    fill: #ffffff;
  }
}

.post--main .post__img {
  @media (min-width: @tablet-width) {
    height: 400px;
  }
}

.post--main img {
  @media (min-width: @tablet-width) {
    height: 400px;
  }
}
